import React, {Component} from 'react';
import './App.css';
import seedrandom from 'seedrandom';

const LS_KEY = "two-card-day-seed";

const CARDS = [
  '🂡 🂢 🂣 🂤 🂥 🂦 🂧 🂨 🂩 🂪 🂫 🂭 🂮'.split(' '),
  '🂱 🂲 🂳 🂴 🂵 🂶 🂷 🂸 🂹 🂺 🂻 🂽 🂾'.split(' '),
  '🃁 🃂 🃃 🃄 🃅 🃆 🃇 🃈 🃉 🃊 🃋 🃍 🃎'.split(' '),
  '🃑 🃒 🃓 🃔 🃕 🃖 🃗 🃘 🃙 🃚 🃛 🃝 🃞'.split(' '),
]


class App extends Component {

  constructor(props) {
    super(props);
    if (!localStorage[LS_KEY]) {
      localStorage[LS_KEY] = Math.random().toString().split('.')[1];
    }
    const seed = localStorage[LS_KEY];
    const date = new Date().toLocaleDateString();

    this.state = {
      seed,
      date
    }

    window.getCard = this.getCard;
    window.CARDS = CARDS;
  }

  getCard = (RNG) => {
    const suit = Math.floor(RNG() * 4);
    const card = Math.floor(RNG() * 13 );
    const isRed = suit === 1 || suit === 2;
    return [CARDS[suit][card], <span data-card-char={CARDS[suit][card]} className={isRed ? "red" : "black"}>{CARDS[suit][card]}</span>];
  };

  getTwoCards = (RNG) => {
    const [card, cardDisplay] = this.getCard(RNG);
    let [card2, card2Display] = this.getCard(RNG);
    while (card2 === card) {
      [card2, card2Display] = this.getCard(RNG);
    }

    return [cardDisplay, card2Display];
  }

  render() {
    const RNG = new seedrandom(this.state.seed+this.state.date);
   const [card, card2] = this.getTwoCards(RNG);

    return (
      <div className="App">
        <header className="App-header">
          <h1>Today's Cards</h1>
          <div className="cards">{card}{card2}</div>
          <div className="seed">seed: {this.state.seed}</div>
        </header>
      </div>
    );
  }
}

export default App;
